import React, {useState} from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { hours } from '../context/data';
import moment from 'moment';
import MainContext from '../context/MainContext';

const Title = styled.h3`

`

const Form = styled.form`
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;

    .submit {
        grid-column: 1 / -1;
        border: none;
        background: var(--blue);
        color: white;
        border-radius: 6px;
        padding: 10px 0;
        margin-top: 10px;
    }
`

const Label = styled.label`
    display: grid;
    grid-template-columns: 1fr;
    gap: 5px;

    &.day,
    &.services {
        grid-column: 1 / -1;
    }


    input,
    select {
        border: 1px solid var(--blue);
        border-radius: 6px;
        padding: 5px 0;
    }

    input[type=text] {
        padding: 5px;
    }
`

export default function AddVisit({ workerName, dateVisit, timeStart, timeEnd }) {
    
    const [newVisit, setNewVisit] = useState({
        worker: workerName,
        date: dateVisit.format('YYYY-MM-DD'),
        start: timeStart,
        end: timeEnd,
        services: 'strzyżenie',
        customer: '',
    })

    const { worker, date, start, end, services, customer } = newVisit;

    const context = React.useContext(MainContext);
    const { customers } = context;

    const handleChange = (e) => {
        setNewVisit({
            ...newVisit,
            [e.target.name]: e.target.value
        })
    }

    const handleSubmit = (e) => {
        e.preventDefault()
        customers.forEach(customer => {
            console.log(customer)
        })
    }

    const findCustomer = (e) => {
        let value = e.target.value;
        let result = value.match(/[a-zA-Z]/i);
        console.log(result)
    }

    return (
        <>
            <Title>Umów wizytę</Title>
            <Form onChange={handleChange} onSubmit={handleSubmit}>
                <Label className="worker">
                    <span>Pracownik: </span>
                    <select defaultValue={worker} name="worker">
                        <option value="Pracownik 1">Pracownik 1</option>
                        <option value="Pracownik 2">Pracownik 2</option>
                        <option value="Pracownik 3">Pracownik 3</option>
                    </select>
                </Label>
                <Label className="customer">
                    <span>Klient: </span>
                    <input type="text" onChange={findCustomer} name="customer" defaultValue={customer} required/>
                </Label>
                <Label className="day">
                    <span>Dzień: </span>
                    <input type="date" name="date" defaultValue={date} />
                </Label>
                <Label className="services">
                    <span>Usługi: </span>
                    <select defaultValue={services} name="services">
                        <option value="strzyżenie">strzyżenie</option>
                        <option value="koloryzacja">koloryzacja</option>
                        <option value="modelowanie">modelowanie</option>
                    </select>
                </Label>
                <Label className="start">
                    <span>Początek wizyty: </span>
                    <select defaultValue={start} name="start">
                        {hours.map(({ start }) => (<option key={start} value={start}>{start}</option>))}
                    </select>
                </Label>
                <Label className="end">
                    <span>Koniec wizyty: </span>
                    <select defaultValue={end} name="end">
                        {hours.map(({ end }) => (<option key={end} value={end}>{end}</option>))}
                    </select>
                </Label>
                <input className="submit" type="submit" value="zapisz"/>
            </Form>
        </>
    )
}

AddVisit.propTypes = {
    workerName: PropTypes.string,
    dateVisit: PropTypes.object,
    timeStart: PropTypes.string,
    timeEnd: PropTypes.string,
}

AddVisit.defaultProps = {
    dateVisit: moment(),
    workerName: 'Pracownik 1',
    timeStart: '07:00',
    timeEnd: '07:30',
}